a, li, ul, b, p{
  all: unset;
}
button{
  all:unset;
  cursor: pointer;
}
body{
  background:linear-gradient( #f73 0%, #f45 100%);
  height:100vh;
}

.App{
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 20px;
}

#stream{
  width: 640px;
  height: 480px;
}
.stream-size{
  display: flex;
  margin-top: 20px;
}
.size-button{
  border-radius: 11px;
  width: 40px;
  height: 40px;
  font-size: 30px;
  display: flex;
  line-height: 33px;
  justify-content: center;
  background-color: #b294e9;
  box-shadow: 0px 2px 1px 2px #000a;
}
.size-button+.size-button{
  margin-left: 40px;
  line-height: 42px;
}

.stream-div{
  display: flex;
  align-items: center;
  flex-direction: column-reverse;
}

.controls{
  margin: 30px;
}

.controls-directions{
  background:linear-gradient( #f84 0%, #f73 100%);
  padding: 15px;
  border-radius: 10px;

  box-shadow: 0 0 7px 1px #0003;
}

.controls-directions-row{
  display: flex;
  justify-content: space-between;
  height: 55px;
  align-items: center;
}

.constrols-button{
  width:  60px;
  height: 45px;
  margin: 7px;
  text-align: center;
  font-weight: 700;
  border-radius: 8px;
  box-shadow: 0px 2px 1px 2px #0007;
  position: relative;
  z-index:5;
}
.constrols-button:active{
  height: 46px;
  box-shadow: 0px 1px 1px 2px #0007;
}
.button-description{
  right: -10px;
  top:-10px;
  z-index: 1;
  font-weight: 600;
  font-size: 12px;
  background-color: #6ff;
  width: max-content;
  padding: 4px;
  border-radius: 5px;
  position: absolute;
}
.byellow{
  background-color: #ff6;
}
.bred{
  background-color: #f66;
}
.bwhite{
  background-color: #fff;
}
.bgray{
  background-color: #777;
  color: #f00;
}
.blblue{
  background-color: #6cf;
}
.bblue{
  background-color: #aaf;
  width: 48px;
  height: 36px;
}
.bblue:active{
  height: 36px;
  box-shadow: 0px 1px 1px 2px #0007;
}
.bwide3{
  width: 200px;
}
.rot3{
  font-size: 30px;
  transform: rotate(270deg);
}
.rot2{
  font-size: 30px;
  transform: rotate(180deg);
}
.rot1{
  font-size: 30px;
  transform: rotate(90deg);
}
.rot{
  font-size: 30px;
  transform: rotate(0deg);
}
.rot35{
  font-size: 30px;
  transform: rotate(315deg);
}
.rot25{
  font-size: 30px;
  transform: rotate(225deg);
}
.rot05{
  font-size: 30px;
  transform: rotate(45deg);
}
.rot15{
  font-size: 30px;
  transform: rotate(135deg);
}

.controls-standup{
  background:linear-gradient( #f84 0%, #f73 100%);
  padding: 15px;
  border-radius: 10px;

  box-shadow: 0 0 7px 1px #0003;
  margin-top: 70px;
}

.pressed-now{
  width: 120px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 24px;
  margin-top: 60px;
  border-radius: 10px;
  color:#fff;
  background-color: #222;
}